import React, { useState, useEffect, useCallback, useRef } from 'react';
import Editor from '@monaco-editor/react';
import alasql from 'alasql';
import { Responsive, WidthProvider } from 'react-grid-layout';
import 'react-grid-layout/css/styles.css';
// import 'react-resizable/css/styles.css';
import './App.css';

// SQL keyword suggestions
const getSqlKeywordSuggestions = (monaco: any) => {
  const keywords = [
    'SELECT', 'FROM', 'WHERE', 'GROUP BY', 'ORDER BY', 'HAVING',
    'JOIN', 'LEFT JOIN', 'RIGHT JOIN', 'INNER JOIN', 'ON', 'AS',
    'DISTINCT', 'COUNT', 'SUM', 'AVG', 'MAX', 'MIN', 'AND', 'OR',
    'IN', 'NOT IN', 'LIKE', 'NOT LIKE', 'IS NULL', 'IS NOT NULL'
  ];

  return keywords.map(keyword => ({
    label: keyword,
    kind: monaco.languages.CompletionItemKind.Keyword,
    insertText: keyword,
    documentation: `SQL keyword: ${keyword}`,
  }));
};

// SQL function suggestions
const getSqlFunctionSuggestions = (monaco: any) => {
  const functions = [
    { name: 'COUNT', description: 'Counts the number of rows' },
    { name: 'SUM', description: 'Calculates the sum of values' },
    { name: 'AVG', description: 'Calculates the average of values' },
    { name: 'MAX', description: 'Finds the maximum value' },
    { name: 'MIN', description: 'Finds the minimum value' },
    { name: 'CONCAT', description: 'Concatenates strings' },
    { name: 'LENGTH', description: 'Returns the length of a string' },
    { name: 'UPPER', description: 'Converts string to uppercase' },
    { name: 'LOWER', description: 'Converts string to lowercase' }
  ];

  return functions.map(func => ({
    label: func.name,
    kind: monaco.languages.CompletionItemKind.Function,
    insertText: `${func.name}()`,
    documentation: func.description,
  }));
};

// Table and column suggestions based on JSON structure
const getTableAndColumnSuggestions = (data: any, monaco: any) => {
  if (!data) return [];

  const suggestions = [];

  // Add table suggestion
  suggestions.push({
    label: '?',
    kind: monaco.languages.CompletionItemKind.Variable,
    insertText: '?',
    documentation: 'JSON data table',
  });

  // Add column suggestions based on JSON structure
  if (Array.isArray(data)) {
    const firstItem = data[0];
    if (firstItem) {
      Object.keys(firstItem).forEach(key => {
        suggestions.push({
          label: key,
          kind: monaco.languages.CompletionItemKind.Field,
          insertText: key,
          documentation: `Column from JSON data`,
        });
      });
    }
  } else {
    Object.keys(data).forEach(key => {
      suggestions.push({
        label: key,
        kind: monaco.languages.CompletionItemKind.Field,
        insertText: key,
        documentation: `Field from JSON data`,
      });
    });
  }

  return suggestions;
};

// Register completion provider
const registerCompletionProvider = (monaco: any, data: any) => {
  // Register new provider with updated data
  return monaco.languages.registerCompletionItemProvider('sql', {
    provideCompletionItems: () => {
      const suggestions = [
        ...getSqlKeywordSuggestions(monaco),
        ...getSqlFunctionSuggestions(monaco),
        ...getTableAndColumnSuggestions(data, monaco),
      ];
      return { suggestions };
    },
  });
};

const ResponsiveGridLayout = WidthProvider(Responsive);

function DetailPanel({ row, onClose }: { row: any, onClose: () => void }) {
  return (
    <div className="detail-panel">
      <div className="detail-panel-header">
        <h2>Row Details</h2>
        <button className="close-button" onClick={onClose}></button>
      </div>
      <Editor
        height="100%"
        defaultLanguage="json"
        value={JSON.stringify(row, null, 2)}
        theme="vs-dark"
        options={{
          minimap: { enabled: false },
          fontSize: 14,
          lineNumbers: 'on',
          roundedSelection: false,
          scrollBeyondLastLine: false,
          readOnly: true,
          automaticLayout: true,
        }}
      />
    </div>
  );
}

function App() {
  const [sqlContent, setSqlContent] = useState<string>('SELECT * FROM ?;');
  const [jsonContent, setJsonContent] = useState<string>('Loading...');
  const [queryResult, setQueryResult] = useState<string>('');
  const [tableData, setTableData] = useState<any[]>([]);
  const [rawData, setRawData] = useState<any>(null);
  const [selectedRow, setSelectedRow] = useState<any>(null);
  const [showDetailPanel, setShowDetailPanel] = useState<boolean>(false);
  const completionProviderRef = useRef<any>(null);
  const monacoRef = useRef<any>(null);

  const handleEditorDidMount = useCallback((editor: any, monaco: any) => {
    monacoRef.current = monaco;
    completionProviderRef.current = registerCompletionProvider(monaco, rawData);
  }, [rawData]);

  useEffect(() => {
    if (monacoRef.current && rawData) {
      if (completionProviderRef.current) {
        completionProviderRef.current.dispose();
      }
      completionProviderRef.current = registerCompletionProvider(monacoRef.current, rawData);
    }
  }, [rawData]);

  useEffect(() => {
    fetch(`${process.env.PUBLIC_URL}/data.json`)
      .then(response => response.json())
      .then(data => {
        setRawData(data);
        setJsonContent(JSON.stringify(data, null, 2));
        executeQuery(sqlContent, data);
      })
      .catch(error => {
        console.error('Error loading JSON:', error);
        setJsonContent('Error loading JSON data');
      });
  }, [sqlContent]);

  const executeQuery = (query: string, data: any) => {
    try {
      const result = alasql(query, [data]);
      setQueryResult(JSON.stringify(result, null, 2));
      setTableData(Array.isArray(result) ? result : [result]);
    } catch (error) {
      console.error('Error executing query:', error);
      setQueryResult(`Error executing query: ${error}`);
      setTableData([]);
    }
  };

  const handleSqlChange = (value: string | undefined) => {
    if (value) {
      setSqlContent(value);
      if (rawData) {
        executeQuery(value, rawData);
      }
    }
  };

  const handleJsonChange = (value: string | undefined) => {
    if (value) {
      try {
        const parsedData = JSON.parse(value);
        setJsonContent(value);
        setRawData(parsedData);
        executeQuery(sqlContent, parsedData);
      } catch (error) {
        console.error('Invalid JSON:', error);
        // Keep the invalid JSON in the editor but don't update the data
        setJsonContent(value);
      }
    }
  };

  const handleRowDoubleClick = (row: any) => {
    console.log('Row clicked:', row);
    setSelectedRow(row);
    setShowDetailPanel(true);
  };

  const handleCloseDetailPanel = () => {
    setShowDetailPanel(false);
    setSelectedRow(null);
  };

  const renderTable = () => {
    if (tableData.length === 0) return null;

    const columns = Object.keys(tableData[0] || {});

    return (
      <div
        className="table-container"
        onMouseDown={(e) => e.stopPropagation()}
        onMouseUp={(e) => e.stopPropagation()}
        onMouseMove={(e) => e.stopPropagation()}
      >
        <table>
          <thead>
            <tr>
              {columns.map((column, index) => (
                <th key={index}>{column}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {tableData.map((row, rowIndex) => (
              <tr
                key={rowIndex}
                onClick={(e) => {
                  e.stopPropagation();
                  handleRowDoubleClick(row);
                }}
                style={{ cursor: 'pointer' }}
              >
                {columns.map((column, colIndex) => (
                  <td key={colIndex} style={{ maxWidth: '200px', overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                    {typeof row[column] === 'object'
                      ? JSON.stringify(row[column])
                      : row[column]}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };

  const layouts = {
    lg: [
      { i: 'query', x: 0, y: 0, w: 12, h: 2, minH: 2 },
      { i: 'result', x: 0, y: 2, w: 12, h: 2, minH: 2 },
      { i: 'result-json', x: 0, y: 2, w: 12, h: 2, minH: 2 },
      { i: 'json', x: 0, y: 4, w: 12, h: 2, minH: 2 }
    ],
    md: [
      { i: 'query', x: 0, y: 0, w: 12, h: 2, minH: 2 },
      { i: 'result', x: 0, y: 2, w: 12, h: 2, minH: 2 },
      { i: 'result-json', x: 0, y: 2, w: 12, h: 2, minH: 2 },
      { i: 'json', x: 0, y: 4, w: 12, h: 2, minH: 2 }
    ],
    sm: [
      { i: 'query', x: 0, y: 0, w: 12, h: 2, minH: 2 },
      { i: 'result', x: 0, y: 2, w: 12, h: 2, minH: 2 },
      { i: 'result-json', x: 0, y: 2, w: 12, h: 2, minH: 2 },
      { i: 'json', x: 0, y: 4, w: 12, h: 2, minH: 2 }
    ]
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1>JSON Viewer</h1>
      </header>
      <main className="App-main">
        <div className={`main-content ${showDetailPanel ? 'with-detail-panel' : ''}`}>
          <ResponsiveGridLayout
            className="layout"
            layouts={layouts}
            breakpoints={{ lg: 1200, md: 996, sm: 768 }}
            cols={{ lg: 12, md: 12, sm: 12 }}
            rowHeight={100}
            margin={[30, 30]}
            containerPadding={[10, 10]}
            isResizable={true}
            isDraggable={true}
          >
            <div key="query" className="editor-section">
              <h2>Query Editor</h2>
              <Editor
                height="100%"
                defaultLanguage="sql"
                value={sqlContent}
                onChange={handleSqlChange}
                theme="vs-dark"
                onMount={handleEditorDidMount}
                options={{
                  minimap: { enabled: false },
                  fontSize: 14,
                  lineNumbers: 'on',
                  roundedSelection: false,
                  scrollBeyondLastLine: false,
                  readOnly: false,
                  automaticLayout: true,
                  suggestOnTriggerCharacters: true,
                  quickSuggestions: {
                    other: true,
                    comments: false,
                    strings: true
                  },
                  tabCompletion: "on",
                }}
              />
            </div>
            <div key="result" className="editor-section">
              <h2>Query Result</h2>
              {renderTable()}
            </div>
            <div key="result-json" className="editor-section">
              <Editor
                height="100%"
                defaultLanguage="json"
                value={queryResult}
                theme="vs-dark"
                options={{
                  minimap: { enabled: false },
                  fontSize: 14,
                  lineNumbers: 'on',
                  roundedSelection: false,
                  scrollBeyondLastLine: false,
                  readOnly: true,
                  automaticLayout: true,
                }}
              />
            </div>
            <div key="json" className="editor-section">
              <h2>Raw JSON Data</h2>
              <Editor
                height="100%"
                defaultLanguage="json"
                value={jsonContent}
                onChange={handleJsonChange}
                theme="vs-dark"
                options={{
                  minimap: { enabled: false },
                  fontSize: 14,
                  lineNumbers: 'on',
                  roundedSelection: false,
                  scrollBeyondLastLine: false,
                  readOnly: false,
                  automaticLayout: true,
                }}
              />
            </div>
          </ResponsiveGridLayout>
        </div>
        {showDetailPanel && (
          <DetailPanel row={selectedRow} onClose={handleCloseDetailPanel} />
        )}
      </main>
    </div>
  );
}

export default App; 
